import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import rightBox from '@/components/common-components/right-box.vue';
import editBox from '@/components/common-components/edit-box.vue';
import addForm from './add-form.vue';
let businessManagement = class businessManagement extends Vue {
    constructor() {
        super(...arguments);
        this.className = '店员管理';
        this.businessUserList = [];
        this.showEditbox = false;
    }
    get shopId() {
        return this.$route.query.shopId;
    }
    get shopName() {
        return this.$route.query.shopName;
    }
    mounted() {
        this.getBusinessUserList();
    }
    addUser() {
        if (this.businessUserList.length >= 30) {
            this.$createDialog({
                type: 'alert',
                title: '最多添加30个店员',
                onConfirm: (e) => { }
            }).show();
            return;
        }
        this.showEditbox = true;
    }
    getBusinessUserList() {
        this.$store.dispatch('getBusinessUserList', { shopId: this.shopId }).then((res) => {
            if (res.success) {
                this.businessUserList = res.data;
            }
        });
    }
    testSend(obj) {
        if (!obj.qwUserId) {
            this.$createDialog({
                type: 'alert',
                content: '店员绑定异常，请联系管理员处理',
                onConfirm: (e) => { }
            }).show();
            return;
        }
        this.$store.dispatch('sendTestMessage', { qwUserId: obj.qwUserId }).then((res) => {
            if (res.success) {
                this.$createDialog({
                    type: 'alert',
                    content: '消息已推送，商家未接收到请联系管理员。',
                    onConfirm: (e) => { }
                }).show();
            }
        });
    }
    addSuccess(msg) {
        this.showEditbox = false;
        this.$createDialog({
            type: 'alert',
            content: msg,
            onConfirm: (e) => { }
        }).show();
        this.getBusinessUserList();
    }
    deleteUser(obj) {
        this.$createDialog({
            type: 'confirm',
            title: '删除确认',
            content: '删除后店员需要重新绑定',
            confirmBtn: {
                text: '确定删除',
                active: true,
                disabled: false,
                href: 'javascript:;'
            },
            cancelBtn: {
                text: '取消',
                active: false,
                disabled: false,
                href: 'javascript:;'
            },
            onConfirm: () => {
                this.$store.dispatch('deleteUserByPhoneOrId', { qwUserId: obj.qwUserId || '', phone: obj.bindPhone || '', shopId: this.shopId }).then((res) => {
                    if (res.success) {
                        this.$createDialog({
                            type: 'confirm',
                            title: '删除成功',
                            onConfirm: (e) => { }
                        }).show();
                        this.getBusinessUserList();
                    }
                    else {
                        this.$createDialog({
                            type: 'confirm',
                            title: res.message,
                            onConfirm: (e) => { }
                        }).show();
                    }
                });
            }
        }).show();
    }
    editUser(obj) {
        console.log(obj, 'ooo');
        this.showEditbox = true;
        this.$nextTick(() => {
            this.$refs.addForm.setData(obj);
        });
    }
};
businessManagement = __decorate([
    Component({
        components: {
            page,
            rightBox,
            editBox,
            addForm
        }
    })
], businessManagement);
export default businessManagement;
