            if (res.message) {
                text = res.message;
            }
            this.$emit('success', text);
        });
    }
    setData({ shopId, name, bindPhone }) {
        if (bindPhone) {
            this.schema.groups[0].fields[1].props['disabled'] = true;
            this.isEdit = true;
        }
